@import './style/helper.scss';

:root {
  --primary-color: #7f7fd5;
  --light-color: #f2f2fb;
  --white-color: #fff;
  --darker-color: #2a2a80;
  --dark-color: #5959c8;
  --light-gray-color: #fcfcfc;
  --gray-color: #aaa;
  --dark-gray-color: #777;
  --black-color: #111;
}

html,
body {
  margin: 0 auto;
  line-height: 1.5;
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

p {
  line-height: 1.75;
}

.section {
  padding: 5rem;
}

@media(max-width: 1024px) {
  html, body {
    font-size: 14px;
  }

  .section {
    padding: 2.5rem;
  }
}